.sectionHeaderWrapper{position:relative;display:flex;align-items:center;justify-content:center;width:100%;min-height:clamp(25rem,60vh,50rem);padding:0;overflow:visible}@media(max-width: 768px){.sectionHeaderWrapper{display:block;min-height:auto}}.image{position:absolute;top:50%;left:0;transform:translateY(-50%);height:100%;width:auto;max-width:none;object-fit:contain;z-index:1;pointer-events:none}@media(max-width: 768px){.image{position:static;transform:none;width:100%;height:auto;object-fit:cover}}.textContainer{position:relative;z-index:2;display:flex;align-items:center;justify-content:center;width:100%;padding:2rem;text-align:center}@media(max-width: 768px){.textContainer{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}}.sectionHeaderText{font-family:var(--font-cormorant);font-size:6rem;font-weight:600;line-height:1.1;color:#f0c332;margin:0;max-width:50rem}@media(max-width: 768px){.sectionHeaderText{font-size:4rem;padding:0 1rem}}